import { getTopKey } from '@/utils'

import {
  VuexModule,
  Module,
  Action,
  Mutation,
} from 'vuex-module-decorators'

import {
  Page,
  Summary,
  IPageConfig,
  State,
  UserAction,
  PersonalityPart,
  UserData,
  Change,
} from '@/@types/types'

import { Archetype } from '@/@types/archetype'

import {
  pageOrder,
  pageConfigs,
} from '@/configs/pageConfig'

import { initializeApp } from 'firebase/app'
import {
  collection,
  getFirestore,
  addDoc,
} from 'firebase/firestore'
const firebaseConfig = {
  apiKey: 'AIzaSyDUmFvOFp_qG-gIci3euMGqN72CLQQ7DFE',
  authDomain: 'ourchetype.firebaseapp.com',
  projectId: 'ourchetype',
  storageBucket: 'ourchetype.appspot.com',
  messagingSenderId: '855914842737',
  appId: '1:855914842737:web:c3781d711b902ead1029d0',
  measurementId: 'G-GG4BG5BDH0',
}
// Initialize Firebase
const app = initializeApp(firebaseConfig)
// Initialize Cloud Firestore and get a reference to the service
const db = getFirestore(app)

@Module({
  name: 'base',
  namespaced: true,
})
export default class Base extends VuexModule {
  // dynamic vars
  stateHistory: State[] = [new State()]
  summary = new Summary()

  get currentState() {
    return this.stateHistory[this.stateHistory.length - 1]
  }

  backgroundColor = 'white'
  @Mutation public setBackgroundColor(color: string) {
    this.backgroundColor = color
  }

  get currentPageIdx(): number {
    if (this.currentState.page)
      return Math.min(
        pageOrder.length - 1,
        pageOrder.indexOf(this.currentState.page) + 1
      )
    return 0
  }
  get currentPageConfig(): IPageConfig {
    return pageConfigs[pageOrder[this.currentPageIdx]]
  }

  // actions and mutations
  @Action({ rawError: true })
  public async submitAction(payload: {
    type: UserAction
    info?: string
    page?: Page | null
    page_detail?: number
    changes?: Change[]
  }) {
    // create new state based on old and changes
    const newState = Object.assign({}, this.currentState)

    newState.session_id = this.summary.session_id
    newState.email = this.summary.user_data.email

    newState.action_type = payload.type
    newState.action_info = payload.info

    const date = new Date()
    newState.date = date.toLocaleDateString()
    newState.time = date.toLocaleTimeString()

    // if set, set to it
    if (payload.page) newState.page = payload.page
    // if undefined, set to next page
    else if (payload.page === undefined)
      newState.page = this.currentPageConfig.page
    // if null, don't change

    // if NOT null, change the page_detail
    if (payload.page !== null)
      newState.page_detail = payload.page_detail

    newState.in_free_mode = this.freeMode

    let personaChanged = false
    payload.changes &&
      payload.changes.map((change) => {
        // persona
        if (change[0] == PersonalityPart.persona) {
          newState.persona_scores[change[1][0]] =
            change[1][1]
          personaChanged = true
        }
        // shadow
        else if (change[0] == PersonalityPart.shadow) {
          newState.top_archetypes.shadow = change[1][0]
        }
        // self
        else if (change[0] == PersonalityPart.self) {
          newState.top_archetypes.self = change[1][0]
        }
      })

    // get top persona archetypes
    if (personaChanged)
      newState.top_archetypes.persona = getTopKey(
        newState.persona_scores
      )!

    // mutate state
    this.mSubmitAction(newState)

    // save to localStorage
    window.localStorage.setItem(
      'ourchetypeState',
      JSON.stringify({
        state: newState,
        summary: this.summary,
      })
    )
  }

  @Mutation private mSubmitAction(state: State) {
    this.stateHistory.push(state)

    // also mutate summary
    this.summary.persona_scores = state.persona_scores
    this.summary.top_archetypes = state.top_archetypes
  }

  @Mutation public submitForm(data: UserData) {
    const date = new Date()

    // set session id from start datetime
    this.summary.session_id = date.toISOString()

    // set start times
    this.summary.start_date = date.toLocaleDateString()
    this.summary.start_time = date.toLocaleTimeString()

    // set the rest
    this.summary.user_data = data
  }

  @Action public async endRun() {
    this.endRunM()

    // upload summary
    const req = addDoc(collection(db, 'Summary'), {
      session_id: this.summary.session_id,
      email: this.summary.user_data.email,
      name: this.summary.user_data.name,
      age: this.summary.user_data.age,
      gender: this.summary.user_data.gender,
      domicile: this.summary.user_data.domicile,
      info_from: this.summary.user_data.info_from,
      start_date: this.summary.start_date,
      start_time: this.summary.start_time,
      end_date: this.summary.end_date,
      end_time: this.summary.end_time,
      p_Ca: this.summary.persona_scores.Ca,
      p_Ev: this.summary.persona_scores.Ev,
      p_In: this.summary.persona_scores.In,
      p_He: this.summary.persona_scores.He,
      p_Ex: this.summary.persona_scores.Ex,
      p_Re: this.summary.persona_scores.Re,
      p_Lo: this.summary.persona_scores.Lo,
      p_Cr: this.summary.persona_scores.Cr,
      p_En: this.summary.persona_scores.En,
      p_Sa: this.summary.persona_scores.Sa,
      p_Ma: this.summary.persona_scores.Ma,
      p_Ru: this.summary.persona_scores.Ru,
      persona: this.summary.top_archetypes.persona,
      shadow: this.summary.top_archetypes.shadow,
      self: this.summary.top_archetypes.self,
      free: this.summary.in_free_mode,
    })
    req.catch((e) => console.log(e))
  }

  @Mutation public endRunM() {
    const date = new Date()
    this.summary.end_date = date.toLocaleDateString()
    this.summary.end_time = date.toLocaleTimeString()

    if (this.freeMode)
      this.summary.in_free_mode = this.freeMode
  }

  // Free testing stuff
  public freeMode = false
  @Action({ rawError: true }) public freeSkip(payload: {
    page?: Page | null
    persona?: Archetype
    shadow?: Archetype
    self?: Archetype
  }) {
    // set free mode
    this.setFreeMode(true)

    // change demo data
    const demoData = new UserData()
    demoData.email = '--testing--'
    this.submitForm(demoData)

    // add action
    const changes: [
      PersonalityPart,
      [Archetype, number]
    ][] = []
    if (payload.persona)
      changes.push([
        PersonalityPart.persona,
        [payload.persona, 1000],
      ])
    if (payload.shadow)
      changes.push([
        PersonalityPart.shadow,
        [payload.shadow, 1000],
      ])
    if (payload.self)
      changes.push([
        PersonalityPart.self,
        [payload.self, 1000],
      ])

    if (payload.page) {
      const idx = pageOrder.indexOf(payload.page) - 1
      payload.page = pageOrder[idx]
    }
    if (!payload.page) payload.page = null

    this.submitAction({
      type: UserAction.free_skipping,
      info: '',
      page: payload.page,
      changes,
    })
  }
  @Mutation private setFreeMode(isFree: boolean) {
    this.freeMode = isFree
  }
}
