import Vue from 'vue'
import VueGtag, { pageview, event } from 'vue-gtag'
import { Page, TopArchetypes } from './@types/types'

Vue.use(VueGtag, {
  config: { id: 'G-GG4BG5BDH0' },
})

pageview({
  page_title: document.title,
  page_location: document.URL,
})

export function openPage(page: Page) {
  event('open_page', { page })
}

export function openConclusion(tops: TopArchetypes) {
  event('open_conclusion', { tops })
}
export function downloadImage(tops: TopArchetypes) {
  event('download_image', { tops })
}
